import React from "react"
import StarIcon from "@assets/svg/LucidIcons/star.svg"
import HeartIcon from "@assets/svg/LucidIcons/heart.svg"
import SunIcon from "@assets/svg/LucidIcons/sun.svg"
import FlowerIcon from "@assets/svg/LucidIcons/flower.svg"
import { Wrapper, Overlay, Content, IconContainer, IconDiv } from "./styled"
import { Typography } from "@mui/material"

const HeroSection: React.FC = () => {
  return (
    <Wrapper
      style={{
        backgroundImage:
          'url("https://images.unsplash.com/photo-1604608672516-f1b9b1d37076?auto=format&fit=crop&w=2000&q=80")',
        backgroundAttachment: "fixed"
      }}
    >
      <Overlay />
      <Content>
        <IconContainer>
          <IconDiv className="yellow-svg">
            <StarIcon />
          </IconDiv>
          <IconDiv className="yellow-red">
            <HeartIcon />
          </IconDiv>
          <IconDiv className="yellow-orange">
            <SunIcon />
          </IconDiv>
          <IconDiv className="yellow-purple">
            <FlowerIcon />
          </IconDiv>
        </IconContainer>
        <Typography variant="h1Bold" component={"div"}>
          Sacred Insignias
        </Typography>
        <Typography variant="h4Medium" component={"div"}>
          Discover the profound meaning behind our spiritual symbols
        </Typography>
      </Content>
    </Wrapper>
  )
}

export default HeroSection
