import React from "react"
import { InsigniaCard } from "./InsignCard"
import { Grid, Section } from "./styled"

export const InsigniasSection: React.FC = () => {
  const insignias = React.useMemo(
    () => [
      {
        title: "Sarva Dharma Symbol",
        description:
          "The symbol represents the unity of all religions, showing how different paths lead to the same divine truth. Each religion's symbol forms a petal of the lotus, emphasizing spiritual harmony.",
        image:
          "https://images.unsplash.com/photo-1604431696980-07e518647bec?auto=format&fit=crop&w=1000&q=80"
      },
      {
        title: "The Lotus Symbol",
        description:
          "The lotus symbolizes purity and spiritual transformation. Growing from mud to bloom in pristine beauty, it represents the journey of the soul from darkness to light.",
        image:
          "https://images.unsplash.com/photo-1616628188467-8f5b38b99d76?auto=format&fit=crop&w=1000&q=80"
      },
      {
        title: "OM Symbol",
        description:
          "The sacred OM symbol represents the primordial sound of creation, the cosmic vibration from which all existence emerged. It symbolizes the unity of body, mind, and soul.",
        image:
          "https://images.unsplash.com/photo-1507692049790-de58290a4334?auto=format&fit=crop&w=1000&q=80"
      },
      {
        title: "Light Symbol",
        description:
          "The light symbol represents divine illumination and wisdom, guiding seekers from ignorance to enlightenment through spiritual knowledge and practice.",
        image:
          "https://images.unsplash.com/photo-1517584367729-e024d844c4c6?auto=format&fit=crop&w=1000&q=80"
      }
    ],
    []
  )

  return (
    <Section>
      <Grid>
        {insignias.map((insignia, index) => (
          <InsigniaCard key={index} {...insignia} />
        ))}
      </Grid>
    </Section>
  )
}
