import { styled } from "@mui/material"
import { orange, purple, red, yellow } from "@mui/material/colors"
import { pxToRem } from "@utils/styles" // Assuming your pxToRem utility

export const Wrapper = styled("div")({
  position: "relative",
  height: "70vh",
  backgroundSize: "cover",
  backgroundPosition: "center",
  display: "flex",
  alignItems: "center",
  justifyContent: "center"
})

export const Overlay = styled("div")({
  position: "absolute",
  inset: 0,
  backgroundColor: "black",
  opacity: 0.5
})

export const Content = styled("div")({
  position: "relative",
  zIndex: 10,
  textAlign: "center",
  color: "white",
  padding: pxToRem(32)
})

export const IconContainer = styled("div")({
  display: "flex",
  justifyContent: "center",
  gap: pxToRem(16),
  "& .yellow-svg": {
    color: yellow["A700"]
  },
  "& .yellow-red": {
    color: red["A200"]
  },
  "& .yellow-orange": {
    color: orange["A700"]
  },
  "& .yellow-purple": {
    color: purple["A700"]
  }
})

export const IconDiv = styled("div", {
  name: "insignias",
  slot: "icon-div"
})({
  width: pxToRem(32),
  height: pxToRem(32)
})
